
















import { Component, Vue } from 'vue-property-decorator'
import { base } from '@/store'
import { Archetype } from '../@types/archetype'
import {
  PersonalityPart,
  UserAction,
} from '../@types/types'

@Component
export default class SelfAnswers extends Vue {
  get archetypes(): string[] {
    return Object.keys(Archetype)
  }

  pickAnswer(arch: Archetype) {
    base.submitAction({
      type: UserAction.answer_question,
      info: arch,
      changes: [[PersonalityPart.self, [arch, 0]]],
    })
  }
}
